import { gql } from '@apollo/client/core'

const admittedSkillList = gql`
  query AdmittedSkillList($student_id: uuid) {
    SkillCertificates(where: { student_id: { _eq: $student_id } }) {
      Course {
        title
        image
        Category {
          name
        }
      }
    }
  }
`

export default admittedSkillList
