<template>
  <Header :title="$t('certificate.title')" />
  <div class="skill-lists-wrap">
    <div
      class="skill-list"
      v-for="(skillCertificate, index) in skillCertificates"
      :key="index"
      :style="{ backgroundImage: 'url(' + skillCertificate.Course.image + ')' }"
    >
      <p class="course-title">{{ skillCertificate.Course.title }}</p>
      <img src="@/assets/stamp.png" alt="" class="stamp" />
      <span class="text-back">{{ skillCertificate.Course.Category.name }}</span>
      <span class="text-front">{{ skillCertificate.Course.Category.name }}</span>
      <ShareButton />
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import Header from '@/components/Header.vue'
import { useQuery, useResult } from '@vue/apollo-composable'
import admittedSkillList from '@/graphql/queries/admittedSkillList'
import { AdmittedSkillListQuery } from '@/graphql/@types/types'
import { useStore } from 'vuex'
import ShareButton from '@/components/ShareButton.vue'

export default defineComponent({
  components: {
    Header,
    ShareButton
  },
  setup() {
    const store = useStore()

    const { result } = useQuery<AdmittedSkillListQuery>(admittedSkillList, {
      student_id: store.state.currentUser?.uid
    })

    const skillCertificates = useResult(result)

    return {
      skillCertificates
    }
  }
})
</script>

